<template>
    <el-card class="box-card" shadow="hover">
      <div slot="header" class="clearfix">
        <span class="fl">页面配置</span>
        <el-button style="padding: 3px 0;margin-left:10px;" class="fr" type="text" size="medium">一键发布</el-button>
        <el-button style="padding: 3px 0" class="fr" type="text" size="medium">资源打包</el-button>
      </div>
      <div class="body">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
          <el-form-item label="页面标题" prop="title">
            <el-input v-model="ruleForm.title"></el-input>
          </el-form-item>
          <el-form-item label="插入图片">
            <el-switch v-model="ruleForm.loadImg"></el-switch>
          </el-form-item>
          <el-collapse-transition>
            <div v-show="ruleForm.loadImg" style="padding-left:100px;margin-bottom:20px;">
              <el-upload
              class="elupload"
              ref="upload"
              list-type="picture-card"
              :action="uploadUrl"
              :limit="10"
              name="imgfile"
              :multiple="true"
              :on-change="uploadChange"
              :auto-upload="false">
                <i class="el-icon-plus" slot="trigger"></i>
              </el-upload>
              <div v-if="fileList.length>0" v-for="(i, idx) in fileList" :key="idx" class="path-wrapper">
                <el-input placeholder="请输入内容" v-model="ruleForm.path[idx]">
                  <template slot="prepend">图片{{idx+1}}链接</template>
                </el-input>
              </div>
            </div>
          </el-collapse-transition>
          <el-form-item label="备注" prop="desc">
            <el-input type="textarea" v-model="ruleForm.desc"></el-input>
          </el-form-item>
          <el-form-item>
            <!-- <el-button type="primary" @click="submitUpload">upload</el-button> -->
            <el-button type="primary" @click="submitForm('ruleForm')">立即预览</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
</template>

<script>
import api from '@/api'
export default {
  name: 'home-form',
  data () {
    return {
      uploadUrl: `${location.origin}/api/uploadImage`,
      fileList: [],
      ruleForm: {
        title: '',
        desc: '',
        loadImg: true,
        path: []
      },
      rules: {
        title: [
          { required: true, message: '请输入页面标题', trigger: 'blur' },
          { min: 3, max: 12, message: '长度在 3 到 12 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
  },
  methods: {
    submitUpload() {
      // this.$refs.upload.submit()
      let fileList = this.fileList.map(i => i.raw)
      return api.uploadImage(fileList)
    },
    createPage (params) {
      return api.createPage(params)
    },
    uploadChange () {
      this.fileList = this.$refs.upload.uploadFiles
    },
    async submitAction () {
      let list = []
      if (this.fileList.length > 0 && this.ruleForm.loadImg) {
        list = await this.submitUpload()
      }
      let params = Object.assign({}, this.ruleForm, {list})
      return await this.createPage(params)
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let loading = this.$loading({
            lock: true,
            text: '正在提交配置，耐心等待～',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          })
          this.submitAction()
          .then(data => {
            console.log('data', data)
            this.$emit('setIfame', data)
          })
          .catch(error => {
            this.$message.error(error);
          })
          .finally(() => {
            loading.close()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

